export default {
	data() {
		return {
			goodList:[],//保存商品列表的数据
		}
	},
	onLoad() {
		this.getGoodList();
	},
	methods:{
		// 请求商品列表
		getGoodList(){
			// uni.showLoading({
			//     title: '加载中'
			// });
			uni.request({
				url:this.$globalVariable.testUrl+'/subject/subjectgoods',
				method: 'POST',
				data: {
					subject_id: 7,
					page: 1,
					page_size:10
				},
				header:this.$globalVariable.requestHeader,
				success: res => {
					console.log(res);
					this.goodList = res.data.result.lists;
					console.log("goodList:",this.goodList);
				},
				fail: (err) => {
					alert(err);
				},
			});
		}
	}
}
